.hot_head {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  background: #16235b;
  position: absolute;
  z-index: 5;
}
.hot_head img {
  width: 36px;
}
.hot_info {
  width: 100%;
  height: 150px;
  display: flex;
  align-items: center;
}
.hot_top {
  position: absolute;
  top: 50px;
  width: 100%;
  z-index: 4;
  background: #0d163f;
}
.hot_info_image {
  max-width: 140px;
  width: 30%;
  padding: 16px;
  height: 100%;
  display: flex;
  align-items: center;
}
.hot_info_image img {
  width: 100%;
}
.hot_info_text {
  margin-left: 8px;
  width: 60%;
  color: #f7f1db;
}
.hot_info_text div {
  margin-top: 8px;
}
.hot_info_text>div:nth-child(3) {
  font-size: 12px;
}
.hot_info_text>div:nth-child(1) {
  font-size: 18px;
}
.hot_button {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: center;
}
.hot_button button {
  margin: 0;
  padding: 0;
  border: 0px solid transparent;
  border-top: 1px solid #0d163f;
  outline: none;
  width: 100%;
  height: 41px;
  background: #16235b;
  color: #f7f1db;
}
.play_btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.play_btn img {
  margin-left: 4px;
}
.play_btn span {
  line-height: 16px;
  font-size: 16px;
}
.hot_song_list_title {
  color: #a29e92;
  font-size: 14px;
  padding: 8px 0 8px 16px;
  background: #0d163f;
}
.hot_song_list_container {
  color: #fff;
}
.hot_song_list_item {
  padding-bottom: 15px;
  text-indent: 1em;
}
.hot_song_list_item:nth-child(odd) {
  background: #16235b;
}
.hot_song_list_item>div:nth-child(1) {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 18px;
  margin-bottom: 5px;
  padding-top: 15px;
}
.hot_song_list_item>div:nth-child(2) {
  font-size: 14px;
  color: #a29e92;
}
.click_loading {
  margin: 20px 0;
  text-align: center;
}
